import React from "react";
import { Formik, Field, Form } from "formik";
import { Route, Switch, Redirect, Link } from "react-router-dom";

import {Button, Menu, MenuItem, } from "@material-ui/core/";

import { FormikField, FormikPassField } from "../../components";

import styles from "./auth.module.scss";

import {ArrowDropDown} from "@material-ui/icons";
import logo from "../../assets/img/Logo.png";
import langRu from "../../assets/img/ruFlag.png";

function Auth() {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <div className={styles.container}>
      <div className={styles.form}>
        <div className={styles.formHeader}>
          <img src={logo} width={96} />
          <div className={styles.headerLangs}>
            <Button
              aria-controls="simple-menu"
              aria-haspopup="true"
              onClick={handleClick}
            >
              <ArrowDropDown fontSize="small" />
              <img src={langRu} />
            </Button>
            <Menu
              id="simple-menu"
              anchorEl={anchorEl}
              keepMounted
              open={Boolean(anchorEl)}
              onClose={handleClose}
            >
              <MenuItem onClick={handleClose}>Русский</MenuItem>
              <MenuItem onClick={handleClose}>Узбекский</MenuItem>
            </Menu>

            
          </div>
        </div>
        <h2 className={styles.formHeading}>Авторизация</h2>
        <Formik
          initialValues={{
            login: "",
            password: "",
          }}
          onSubmit={async (values) => {
            await new Promise((r) => setTimeout(r, 500));
            //alert(JSON.stringify(values, null, 2));

           
          }}
        >
          <Form className={styles.formGroup}>
            <FormikField name="login" label="Логин" variant="outlined" style={{marginBottom: 20}} />
            <FormikPassField name="password" label="Пароль" style={{marginBottom: 36}} />
          <Link to="/">

          
            <Button variant="contained" type="submit" color="primary">Войти</Button></Link>
          </Form>
        </Formik>
      </div>
    </div>
  );
}

export default Auth;
